body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ant-carousel .slick-slide {
    text-align: center;
    height: 500px;
    /* line-height: 250px; */
    padding-top: 100px;
    background: #0c49178c;
    overflow: hidden;
  }
  
    .ant-carousel .slick-slide input {
    width: 10%;
    background: #8d9cb8;
    text-align: center;

  }
  .col1 {
    text-align: center;
    padding-left: 29%
  }
  /* .col2 {
    text-align: center;
    padding-right: 35%
  } */
  hr {
    width:50%
  }

  .slider {
    width: 90%;
    align-content: center
    }
  .div{
    align-content: right;
  }
  /* .ant-carousel .slick-slide h3 {
    color: #fff;
  } */

  

.trigger {
    font-size: 18px;
    line-height: 64px;
    padding: 0 24px;
    cursor: pointer;
    -webkit-transition: color .3s;
    transition: color .3s;
  }
  
.trigger:hover {
    color: #1890ff;
  }
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.custom-icon{
  font-size: 35px !important;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.image{
  background-image: url("https://wallpaper.wiki/wp-content/uploads/2017/05/wallpaper.wiki-HD-Desktop-Plain-Images-PIC-WPE007976.jpg") !important;

}
